import React , { Component } from 'react';
import Header from './Header.js';
import './AddSubscriber.css'
import {Link} from 'react-router-dom'; 

class AddSubscriber extends Component{
constructor()
{
	super();
	this.state={
		id:0,
		runs:"",balls:"",  //initialising
		count:0

	}
	this.handleClick=this.handleClick.bind(this)
}
inputChangeHandler=(e) =>{
	const state=this.state;
	state[e.target.name]=e.target.value;
	this.setState(state);
}
onFormSubmitted =(e)=>
{
	
e.preventDefault();
this.props.addSubscriberHandler(this.state);
this.setState({id: 0,name: ' ', phone: ' '});
this.props.history.push('/');
}

handleClick(){
	console.log("clicked")
    this.setState(prevState=>{
        return{
count:prevState.count+10
        }
    }
        )
        console.log(this.state);
}



	render()
	{
//		const{name,phone}=this.state;
		return(
<div>

{/* <h1>{this.state.count}</h1>
            <button >increase</button> */}

<Header namee="Add Match data"/>
<div className="component-body-container">
<Link to="/"><button className="custom-btn">Back</button></Link> 
<form className="subscriber-form" onSubmit={this.onFormSubmitted.bind(this)}>

                        <label htmlFor="runs" className="label-control">Runs: </label><br/>

                        <input id="runs" type="number" className="input-control" name="runs" onChange={this.inputChangeHandler}/><br/><br/>

                        <label htmlFor="balls" className="label-control">Balls: </label><br/>

                        <input id="balls" type="number" className="input-control" name="balls" onChange={this.inputChangeHandler}/><br/><br/>

						<label htmlFor="outway" className="label-control">How out: </label><br/>

<input id="outway" type="text" className="input-control" name="outway" onChange={this.inputChangeHandler}/><br/><br/>
<label htmlFor="outway" className="label-control">REMARKS: </label><br/>

<input id="remarks" type="text" className="input-control" name="remarks" onChange={this.inputChangeHandler}/><br/><br/>


                        <div className="subscriber-info-container">

                            <span className="subscriber-to-add-heading">Subscriber to be added: </span><br/>

                            <span className="subscriber-info">Runs:{this.state.runs} </span><br/>

                            <span className="subscriber-info">Balls:{this.state.balls} </span><br/>

                            <span className="subscriber-info">Outway:{this.state.outway} </span><br/>
                            <span className="subscriber-info">remarks:{this.state.remarks} </span><br/>

                        </div>



                        <button type="submit" className="custom-btn add-btn" >Add</button>

                    </form>
					<button onClick={this.handleClick.bind(this)} >click</button>

</div>
</div>
			)
	}
}

export default AddSubscriber;