import React,{Component} from 'react';
class Aaaa extends Component{
	constructor()
	{
		super();
		this.state={
			coins:0,flag:true
		};
	}

	Inc =()=>{
		this.setState({coins:this.state.coins+1});
	console.log(this.state);
	}
	render(){

		return(
			
			<div><button onClick={this.Inc}>abhay</button>
				<div>{this.state.coins}</div>
				if(flag==true){console.log("true")}
				</div>

			);
	}
}
export default Aaaa;