import React,{Component} from 'react';
import AddSubscriber from './AddSubscriber';
import ShowSubscriber from './ShowSubscriber';
import {BrowserRouter as Router,Route} from 'react-router-dom';
class PhoneDirectory extends Component{
    constructor(){
        super();
        this.state={
            
            subscribersList:[
                {
                    id:1,runs:'58',balls:'57',strikerate:'101',outway:'caught',remarks:'change strike'
                },
                {
                    id:2,runs:'99',balls:'75',strikerate:'130',outway:'bowled',remarks:'dont go for sixes early'
                },
                
                   
                
            ]
        }
    }

    deleteSubscriberHandler = (subscriberId) => {
        let subscribersList = this.state.subscribersList;
        let subscriberIndex = 0;
        subscribersList.forEach(function (subscriber, index) {
            if (subscriber.id === subscriberId) {
                subscriberIndex = index;
            }
        }, this);
        let newSubscribers = subscribersList;
        newSubscribers.splice(subscriberIndex, 1);
        this.setState({subscribers: newSubscribers});
    }

    addSubscriberHandler = (newSubscriber) => {
        let subscribersList = this.state.subscribersList;
        if (subscribersList.length > 0) {
            newSubscriber.id = subscribersList[subscribersList.length - 1].id + 1;
        } else {
            newSubscriber.id = 1;
        }
        subscribersList.push(newSubscriber);
        this.setState({ subscribersList: subscribersList });
        // console.log(this.state.subscribersList);
    }

render(){
    
    return (
//     // <AddSubscriber addSubscriberHandler={this.addSubscriberHandler}/>)
//    <ShowSubscriber subscribersList={this.state.subscribersList}/>
<Router basename="/CryptoWikiproject">
    <Route exact path="/" render={(props)=><ShowSubscriber {...props}subscribersList={this.state.subscribersList} deleteSubscriberHandler={this.deleteSubscriberHandler}/>}/>
    <Route exact path="/add" render={({history},props)=><AddSubscriber history={history}{...props}addSubscriberHandler={this.addSubscriberHandler} />}/>
    
</Router> 
    )
}
}
export default PhoneDirectory;