import React,{Component} from 'react';




      const headerStyle = {

        textAlign: 'right',

        padding: 20,

        background: '#000',

        color: '#fff',

        textTransform: 'uppercase'
      }

class Header extends Component{
    render(props){
        return (

        <div style={headerStyle}>
           <h1>{this.props.namee}</h1> <h3>abhinav sharma</h3>
        </div>

    )
    }
}





export default Header; 