import React , { Component } from 'react';
import './ShowSubscriber.css'
import Header from './Header.js';
// import Average from './Average.js';
import {Link} from 'react-router-dom'; 
class ShowSubscriber extends Component{
    constructor()
    {
        super(); 
    this.state = { 
      count:100 
    } 
    }

onDeletedClick = (subscriberId) => {
        this.props.deleteSubscriberHandler(subscriberId); 
      }
      



render()
{ 
    let t=0;
    var d = new Date();
    var x= d.getDate();
    var y=d.getMonth();
    var z=d.getFullYear()
    return(
        <div>
        <Header namee="Your Progress"/>
        <div className="component-body-container">
           <Link to="/add"> <button className="custom-btn add-btn" onClick={()=>{console.log(this.state)}}>Add</button></Link>




            <div className="grid-container heading-container">
            <span className="grid-item phone-heading">Date</span>    
                <span className="grid-item name-heading">Runs</span>
                <span className="grid-item phone-heading">Balls</span>
                <span className="grid-item phone-heading">Strike rate</span>
                <span className="grid-item phone-heading">how out</span>
                <span className="grid-item phone-heading">Remarks and learning</span>
                
                
                

            </div>



       
{
this.props.subscribersList.map(sub=>{
    // t+=sub.runs;
    return <div key={sub.id} className="grid-container" >
<span className="grid-item" onChange={this.Av}>{x}/{y}/{z}</span>
                <span className="grid-item" >{sub.runs}+{this.state.total}</span>
                <span className="grid-item">{sub.balls}</span>
                <span className="grid-item">{((sub.runs/sub.balls)*100).toFixed(2)}</span>
                <span className="grid-item">{sub.outway}</span>
                <span className="grid-item">{sub.remarks}</span>


                <span className="grid-item"><button className="custom-btn delete-btn" onClick={this.onDeletedClick.bind(this, sub.id)}>delete</button></span>
            </div>
                           
                        
                
})
}


        </div>
        
    </div>
)
}}

export default ShowSubscriber;